<template>
  <div class="module" v-loading="!myData">
    <module-name :title="title" :link="link"></module-name>
    <template v-if="myData">
      <div class="main">
        <div class="data">
          <p class="member"><span>计划验收</span>{{ myData.num }}</p>
          <p class="member"><span>实际验收</span>{{ myData.done_num }}</p>
        </div>
        <a-progress
          type="circle"
          :stroke-color="{
            '0%': '#00C4FF',
            '100%': '#009AFB',
          }"
          :width="windowSize.height * 0.1"
          :strokeWidth="14"
          :percent="myData.done_rate"
          status="normal"
          gapPosition="bottom"
        >
          <template v-slot:format="percent">
            <p class="progress-percent">{{ percent }}<span>%</span></p>
          </template>
        </a-progress>
      </div>
      <div class="progress-box">
        <my-progress-dot
          v-for="item in topFive"
          :key="item.id"
          :name="item.name"
          :percentage="item.done_rate"
        ></my-progress-dot>
      </div>
    </template>
  </div>
</template>

<script>
import moduleName from "@/components/moduleName";
import myProgressDot from "@/components/myProgressDot";
export default {
  name: "windowTwo",
  components: {
    myProgressDot,
    moduleName,
  },
  props: {
    title: String,
    link: String,
    myData: Object,
    windowSize: Object,
  },
  data() {
    return {
      topFive: [],
    };
  },
  watch: {
    "myData.company": {
      handler(val) {
        if (val) {
          this.topFive = val.slice(0, 5);
        }
      },
      immediate: true,
    },
  },
};
</script>

<style scoped>
.main {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  height: 10vh;
}
.data {
  width: 10vw;
  margin-left: 3.5vw;
  text-align: right;
}
.member {
  font-size: 2.2vmin;
  font-weight: bold;
  color: #000000;
}
.member > span {
  font-size: 1.8vmin;
  color: rgba(0, 0, 0, 0.5);
  margin-right: 6px;
}
.progress-box {
  margin-top: 1vh;
}
.progress-percent {
  font-size: 2.2vmin;
  color: #000000;
  font-weight: bold;
}
.progress-percent > span {
  font-weight: 400;
  font-size: 1.6vmin;
}
</style>
