<template>
  <div class="module-name">
    <a :href="link">{{title}}</a>
  </div>
</template>

<script>
export default {
name:"moduleName",
props:{
  title:String,
  link:String
},
methods:{
  toWeb(){
    if(this.link){
      sessionStorage.setItem("webLink",this.link);
      this.$router.push({path:'/Web'});
    }
  }
}
}
</script>

<style scoped>
.module-name>a {
  font-size: 2.4vmin;
  color: rgba(0, 0, 0, 0.85);
  font-weight: 600;
}
</style>