            :editable="false"
            :clearable="false"
            size="short"
            prefix-icon="hide"
            @change="dateChange"
          ></el-date-picker>
          <i class="el-icon-arrow-down"></i>
        </div>
      </template>
    </my-header>
    <div class="window">
      <div class="left">
        <window-one
          :my-data="progress"
