          :link="
            `${link}&industrial=建筑&startdate=${startDate}&enddate=${endDate}&plate=jdgl`
          "
          :window-size="windowSize"
        ></window-one>
        <window-two
          :my-data="accept"
          title="质量验收"
          :link="
            `${link}&industrial=建筑&startdate=${startDate}&enddate=${endDate}&plate=zlys`
          "
          :window-size="windowSize"
        ></window-two>
        <window-three
          :my-data="reform"
          title="项目整改"
          :link="
            `${link}&industrial=建筑&startdate=${startDate}&enddate=${endDate}&plate=xmzg`
          "
          :window-size="windowSize"
        ></window-three>
      </div>
      <div class="middle">
        <window-four
          :my-data="aq"
          title="安全"
          :link="
            `${link}&industrial=建筑&startdate=${startDate}&enddate=${endDate}&plate=aq`
          "
