          :color="['#FFB200', '#FB8D00']"
        ></window-four>
        <window-four
          :my-data="zl"
          title="技术质量"
          :link="
            `${link}&industrial=建筑&startdate=${startDate}&enddate=${endDate}&plate=jszl`
          "
          :window-size="windowSize"
