<script>
// @ is an alias to /src
import myHeader from "@/components/myHeader";
import windowOne from "@/components/windowOne";
import windowTwo from "@/components/windowTwo";
import windowThree from "@/components/windowThree";
import windowFour from "@/components/windowFour";
import windowFive from "@/components/windowFive";
import windowSix from "@/components/windowSix";
import Masker from "@/components/masker";
export default {
  name: "Home",
  components: {
    myHeader,
    windowOne,
    windowTwo,
    windowThree,
    windowFour,
    windowFive,
    windowSix,
    Masker
  },
  data() {
    return {
      progress: null,
      accept: null,
      reform: null,
      checkin: null,
      entry: null,
      register: null,
      sc: null,
      aq: null,
      zl: null,
      windowSize: {
        width: window.screen.availWidth,
        height: window.screen.availHeight,
      },
      showDate: "08",
      dateStr: "2020-08",
      startDate: "2020-07-26",
      endDate: "2020-08-25",
      link: `http://proxy.zhongnangroup.cn/fr/decision/view/report?viewlet=7%25E8%25AE%25BE%25E8%25AE%25A1%25E7%25A8%25BF%252F%25E7%258E%258B%25E9%259B%25AA%25E8%258E%2589%252F%25E8%25A7%2584%25E8%258C%2583%25E8%2580%2583%25E6%25A0%25B8%252F%25E6%25A3%2580%25E6%259F%25A5%25E6%258A%25A5%25E8%25A1%25A8_%25E5%258C%25BA%25E5%259F%259F%25E5%2585%25AC%25E5%258F%25B8.cpt&ref_t=design&ref_c=45577d7d-16d4-4062-8c9d-3b8befdddcb6`,
    };
  },
  methods: {
    dateChange(date) {
      console.log(date);
      this.dateStr = this.formatDate(date, "yyyy-MM");
      this.endDate = this.getEndDate(date);
      this.startDate = this.getStartDate(date);
      this.getData();
    },
    getNowDate() {
      const date = new Date();
      const day = date.getDate();
      if (day > 25) {
        this.dateStr = this.formatDate(date, "yyyy-MM");
        this.endDate = this.getEndDate(date);
        this.startDate = this.getStartDate(date);
      } else {
        date.setMonth(date.getMonth() - 1);
        this.dateStr = this.formatDate(date, "yyyy-MM");
        this.endDate = this.getEndDate(date);
        this.startDate = this.getStartDate(date);
      }
    },
    getEndDate(date) {
      //当月25号
      date.setDate(25);
      return this.formatDate(date);
    },
    getStartDate(date) {
      //前月26号
      date.setMonth(date.getMonth() - 1);
      date.setDate(26);
      return this.formatDate(date);
    },
    formatDate(date, format = "yyyy-MM-dd") {
      let year = date.getFullYear();
      let month = date.getMonth() + 1;
      let day = date.getDate();
      switch (format) {
        case "yyyy-MM-dd": //2020-08-25
          return [year, month, day].map(this.formatNum).join("-");
        case "yyyy-MM": //2020-08
          return [year, month].map(this.formatNum).join("-");
      }
    },
    formatNum(num) {
      return num < 10 ? `0${num}` : num;
    },
    getData() {
      const axios = this.$axios;
      const params = {
        date: this.dateStr,
        company_id: "list",
      };
      const http = [
        {
          api: "/api/temporary/specs/progress",
          target: "progress",
        },
        {
          api: "/api/temporary/specs/accept",
          target: "accept",
        },
        {
          api: "/api/temporary/specs/reform",
          target: "reform",
        },
        {
          api: "/api/temporary/specs/checkin",
          target: "checkin",
        },
        {
          api: "/api/temporary/specs/entry",
          target: "entry",
        },
        {
          api: "/api/temporary/specs/register",
          target: "register",
        },
        {
          api: "/api/temporary/specs/sc",
          target: "sc",
        },
        {
          api: "/api/temporary/specs/aq",
          target: "aq",
        },
        {
          api: "/api/temporary/specs/zl",
          target: "zl",
        },
      ];
      for (const iterator of http) {
        this[iterator.target] = null;
        axios
          .get(iterator.api, {
            params,
          })
          .then((res) => {
            // console.log(res);
            const { code, data } = res.data;
            if (code == 0) {
              this[iterator.target] = data;
            }
          });
      }
    },
  },
  mounted() {
    this.getNowDate();
    this.getData();
  },
};
</script>
