<template>
  <div class="module" v-loading="!myData">
    <module-name :title="title" :link="link"></module-name>
    <template v-if="myData">
      <div class="main">
        <div class="square">
          <a-progress
            type="circle"
            :stroke-color="{
              '0%': '#00C4FF',
              '100%': '#009AFB',
            }"
            :width="windowSize.height * 0.1"
            :strokeWidth="14"
            :percent="myData.all_done_rate"
            status="normal"
            gapPosition="bottom"
          >
            <template v-slot:format="percent">
              <p class="progress-percent">{{ percent }}<span>%</span></p>
            </template>
          </a-progress>
          <p class="title">累计闭环率</p>
        </div>
        <div class="square">
          <a-progress
            type="circle"
            :stroke-color="{
              '0%': '#00C4FF',
              '100%': '#009AFB',
            }"
            :width="windowSize.height * 0.1"
            :strokeWidth="14"
            :percent="myData.done_rate"
            gapPosition="bottom"
          >
            <template v-slot:format="percent">
              <p class="progress-percent">{{ percent }}<span>%</span></p>
            </template>
          </a-progress>
          <p class="title">月闭环率</p>
        </div>
        <div class="data">
          <p class="member"><span>问题数</span>{{ myData.done_num }}</p>
          <p class="member"><span>已整改</span>{{ myData.num }}</p>
        </div>
      </div>
      <horizontal-rank :rank="myData.company"></horizontal-rank>
    </template>
  </div>
</template>

<script>
import moduleName from "@/components/moduleName";
import horizontalRank from "@/components/horizontalRank";
export default {
  name: "windowThree",
  components: {
    horizontalRank,
    moduleName,
  },
  props: {
    title: String,
    link: String,
    myData: Object,
    windowSize: Object,
  },
};
</script>

<style scoped>
.main {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 1.6vh;
  margin-bottom: 1.2vh;
  /* height: 16vh; */
}
.main > .square > .title {
  font-size: 1.6vmin;
  color: rgba(0, 0, 0, 0.5);
  text-align: center;
  margin-top: 1.6vh;
}
.main > .data {
  margin-top: -3vh;
}
.main > .data > .member {
  color: #000000;
  text-align: right;
  font-weight: bold;
  font-size: 2.2vmin;
}
.main > .data > .member > span {
  color: rgba(0, 0, 0, 0.5);
  font-size: 1.6vmin;
  margin-right: 1vw;
}
.progress-percent {
  font-size: 2.2vmin;
  color: #000000;
  font-weight: bold;
}
.progress-percent > span {
  font-weight: 400;
  font-size: 1.6vmin;
}
</style>
