<template>
  <div v-if="page < masks.length">
    <template v-for="(mask, index) in masks">
      <div class="masker" :key="index" v-if="page == index">
        <div class="selecter" v-html="mask.htl"></div>
        <div class="board">
          <p class="message">{{ mask.text }}</p>
          <div class="buttom" @click="next">我知道了</div>
        </div>
      </div>
    </template>
  </div>
</template>
<script>
export default {
  name: "Masker",
  props:{
    dataStr: String
  },
  data() {
    return {
      masks: [
        {
          text: "点击标题查看各模块BI报表",
          htl: `<p class="shower-0">进度管理</p><i class="iconfont jiantou-0">&#xe6be;</i>`,
        },
        {
          text: "点击切换月份数据",
          htl:`<p class="shower-1"><span>2020-08</span><i class="el-icon-arrow-down"></i></p><i class="iconfont jiantou-1">&#xe6be;</i>`,
        },
        {
          text: "点击查看BI报表",
          htl: `<p class="shower-2"><i class="iconfont">&#xe600;</i><span>BI报表</span></p><i class="iconfont jiantou-2">&#xe6be;</i>`,
        },
      ],
      page: 0,
    };
  },
  methods: {
    next() {
      localStorage.setItem("maskerIndex", ++this.page);
    },
  },
  mounted() {
    this.page = Number(localStorage.getItem("maskerIndex")) || 0;
  },
};
</script>

