  height: 100vh;
  z-index: 9999;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  user-select: none;
}
.board {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}
.message {
  font-size: 4vmin;
  color: white;
  font-weight: 600;
}
.buttom {
  margin: 20px auto 0;
  background-color: #0091ff;
  border: none;
  padding: 0.5vh 1vw;
  display: inline-block;
  color: white;
  font-size: 2.4vmin;
  border-radius: 1vh;
  cursor: pointer;
}
.selecter>>>.shower-0 {
  background-color: white;
  border-radius: 50%;
  padding: 0.4vh 1vw;
  line-height: 4vh;
  font-weight: bold;
  font-size: 2.4vmin;
  /* cursor: pointer; */
  position: absolute;
  left: 1vw;
  top: 8vh;
  white-space: nowrap;
}
.selecter>>>.jiantou-0 {
  position: absolute;
  color: white;
  top: 12vh;
