}
.selecter>>>.shower-2 > i {
  margin-right: 0.2vw;
  font-size: 2.2vmin;
}
.selecter>>>.jiantou-2 {
  position: absolute;
  color: white;
  top: 7vh;
  right: 4vw;
  font-size: 38vmin;
}
